@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Montserrat;
}

.App {
  text-align: center;
}


header{
    background: #007C00;
}

header > h1{
    display: inline;
    text-decoration: underline;
    text-underline-offset: 25px;
    text-decoration-thickness: 1px;
    font-size: 4rem;
    font-weight: bolder;
}

header h1, header h2{
    color: white;
}

header article{
    display: flex;
    justify-content: space-around;
    padding: 0 0 5% 0;
}

article h2{
    font-size: 2.5rem;
}

article p{
    font-size: .9rem;
    font-weight: bolder;
}

header > p:nth-of-type(1){
    padding: 3% 0;
    font-size: 1.7rem;
    font-weight: 700;
}

header p:nth-of-type(2){
    padding: 6% 8% 4% 8%;
    font-size: 1.1rem;
    line-height: 3.3vh;
    font-weight: 400;
}

header div h2{
    font-size: 5rem;

}

header div p{
    font-size: 1.2rem;
}

header div:not(header div:nth-of-type(4)){
    border-right: 1px solid white;
    padding: 0 5% 0 0;
}
section{
    padding: 3% 0 0 0;
}

section h3{
    color: #56AE63;
    font-size: 1.5rem;
}

section h2{
    font-weight: bolder;
    padding: 1% 16% 3% 16%;
    font-size: 2.5rem;
}

section article{
    display: grid;
    grid-template-columns: 1fr 1fr;
    text-align: left;
    grid-gap: 25px;
    padding: 0 0% 0 12%;
}

section article div{
    width: 80%;
}

section article div img{
    margin-right: 20px;
    margin-left: -50px;
    margin-bottom: -10px;
}

section article div p{
    line-height: 3.5vh;
    font-weight: 500;
    font-size: 1rem;
}

section article h4{
    font-size: 1.5rem;
}

footer{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin: 5% 5% 0 5%;
    background: grey;
    padding: 0 5%;
}

footer img{
    width: 250px;
    height: 200px;
}

footer div{
    background: white;
    padding: 0 0 10% 0;
    text-align: left;
}

footer div:nth-of-type(1), footer div:nth-of-type(5){
    padding: 0 0 0 5%;
}

footer div:nth-of-type(4), footer div:nth-of-type(8){
    margin: 0 5% 0 0;
}

footer h4{
    color: dodgerblue;
    padding: 7% 0 11% 0;
    font-size: 2.8vh;
}

footer h5{
    color: yellowgreen;
    font-size: 2.8vh;
}

footer p{
    line-height: 3.8vh;
    padding: 0 5% 10% 0%;
    font-size: 3vh;
}
